<template>
  <div id="single-blog">
    <h1>{{ blog.title }}</h1>
    <article>{{ blog.content }}</article>
    <p>作者：{{ blog.author }}</p>
    <p>分类：</p>
    <ul>
      <li v-for="category in blog.categories" v-bind:key="category">
        {{ category }}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
    };
  },
  created() {
    var that = this;
    this.axios
      .get(
        "https://vuedemo-252e0-default-rtdb.firebaseio.com/posts/" +
          this.id +
          ".json"
      )
      .then((res) => {
        return res.data;
      })
      .then(function (data) {
        that.blog = data;
      });
  },
};
</script>
<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>