<template>
  <div v-theme="'wide'" id="show-blog">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" />
    <div v-for="blog in filteredBlogs" class="single-blog" v-bind:key="blog">
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-rainbow="'#' + Math.random().toString(16).slice(2, 8)">
          {{ blog.title.toUpperCase() }}
        </h2>
      </router-link>
      <article>
        {{ blog.content.slice(0, 100) }}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blog",
  data() {
    return {
      blogs: [],
      search: "",
    };
  },
  created() {
    var that = this;
    this.axios
      .get("https://vuedemo-252e0-default-rtdb.firebaseio.com/posts.json")
      .then((res) => {
        return res.data;
      })
      .then(function (data) {
        var blogsArray = [];
        for (let key in data) {
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        console.log(blogsArray);
        that.blogs = blogsArray;
      });
  },
  computed: {
    filteredBlogs: function () {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search);
      });
    },
  },
};
</script>

<style scoped>
#show-blog {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>
